<template>
<transition name="slide">
  <div class="g-search">
    <header class="m-header">
        <div class="u-icon-back" @click="goBack">
          <i class="icon iconfont icon-fanhui"></i>
        </div>
        <div class="search">
          <input type="text" placeholder="搜索商品名称">
        </div>
        <div class="u-icon-search">
          <i class="icon iconfont icon-xiazai5"></i>
        </div>
    </header>
    <div class="search-content-wapper">
      <div class="hot-search">
      <div class="title">热门搜索</div>
        <div class="banner">
          <img :src="bannerImg" alt="广告图片">
        </div>
        <div class="hot-key" v-if="hotKeys.length">
          <span v-for="item in hotKeys" :key="item.id">{{item}}</span>
        </div>
      </div>
    </div>
  </div>
</transition>
</template>
<script>
import { mapMutations } from 'vuex'
export default {
  name: 'search',
  data () {
    return {
      hotKeys: [1, '你好呀', '是的', '你好呀', '是的'],
      bannerImg: ''
    }
  },
  created () {
    this.getSearchDefault()
  },
  components: {
  },
  methods: {
    ...mapMutations([
      'set_isNavRouter'
    ]),
    goBack () {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    },
    getSearchDefault () {
      this.bannerImg = 'https://i8.mifile.cn/b2c-mimall-media/38af6033fa4409e3844029d76b60cdc6.jpg?bg=FFFFFF'
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      // 通过 `vm` 访问组件实例
      console.log(to)
      console.log('=========beforeRouteEnter')
      vm.set_isNavRouter(false)
    })
  },
  beforeRouteLeave (to, from, next) {
    this.set_isNavRouter(true)
    next()
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
  @import "~common/scss/iconfont";
  @import "~common/scss/variable";
  @import "~common/scss/minxin";
  .g-search{
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 100;
    width: 100%;
    background-color: #fff;
    &.slide-enter-active, &.slide-leave-active{
      transition: all 0.3s
    }
    &.slide-enter, &.slide-leave-to{
      transform: translate3d(100%, 0, 0)
    }
    .m-header {
      display: flex;
      align-items: center;

      height: $h-height;
      padding: 0 $lr-space;
      background-color: $color-background;
      .search{
        flex: 1;
        height: 26px;
        line-height: 26px;
        background: #fff;
        border: 1px solid $color-border;

        text-align: left;
        color: #959595;
        font-size: $font-size-small-s;
        input{
          box-sizing: border-box;
          width: 100%;
          height: 100%;

          padding-left: 5px;
          padding-right: 5px;
        }
      }
      .u-icon-back{
        margin-right: 10px;
      }
      .u-icon-search{
        margin-left: 10px;
      }
      .icon{
        font-size: $font-size-large;
        color: $color-text-title;
      }
    }
    .search-content-wapper {
      .hot-search{
        font-size: $font-size-small;
        @include lr-space();
        .title {
          color: $color-text;
          padding-top: 10px;
          padding-bottom: 10px;
        }
        .banner{
          @include no-lr-space();
          img {
            width: 100%;
          }
        }
        .hot-key{
          @include no-lr-space();
          span {
            display: inline-block;
            color: #884c4c;
            margin: 8px 10px;
            padding: 4px 10px;
            line-height: 1;
            border: 1px solid #e4e4e4;
            background-color: #fff9f8;
          }
        }
      }
    }
  }
</style>



